.ExModal {
  z-index: 2;
  width: 90%;
  height: auto;
  padding: 10px;
  background: #fff;
  border: 0.1rem solid #000;
  border-radius: 10px;
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
  h1 {
    font-size: 3rem;
    margin: 10px 0;
    word-break: keep-all;
  }
  h2 {
    font-size: 2.7rem;
    text-align: center;
  }

  button {
    width: 20rem;
    background-color: white;
    padding: 0.5rem 1rem;
    border-radius: 0.5rem;
    font-size: 2rem;
    margin: 2rem auto;
  }
  .trialList {
    border: 0.1rem solid #000;
    width: 90%;
    border-radius: 1rem;

    font-size: 2rem;
    padding: 0.8rem;
  }
}
