.exBtnGroup {
  position: absolute;
  width: 25rem;
  left: 50%;
  top: 8rem;
  transform: translateX(-50%);
  .exBtn {
    width: 8rem;
    height: 3rem;
    margin-right: 0.5rem;
    margin-bottom: 0.5rem;
    color: black;
    cursor: pointer;
    font-family: "Noto Sans", sans-serif;
    font-size: 2rem;
    font-weight: 500;

    border-radius: 0.5rem;
  }
  .exBtn:nth-of-type(3) {
    margin-right: 0;
  }
  .exBtn:nth-of-type(6) {
    margin-right: 0;
  }

  .selected {
    background: rgba(11, 195, 11, 0.74);

    font-weight: 600;
  }
}
