.background {
  width: 100%;
  height: 100%;
  z-index: 0;
  position: absolute;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(0.2rem);
}

.modal {
  font-size: 2rem;
  z-index: 2;
  position: absolute;
  height: 50rem;
  left: 50%;
  transform: translateX(-50%);
  padding: 1rem;
  top: 8rem;
  background: #fff;
  border: 0.1rem solid #000;
  border-radius: 1rem;
  width: 30rem;

  h1 {
    font-size: 3rem;
    word-break: keep-all;
    text-align: center;
  }
  .close {
    position: absolute;
    bottom: 0;
  }

  h3 {
    margin: 10px 0;
  }
  .group {
    position: relative;
    height: 9rem;
    background-color: lightyellow;
    padding: 1rem;
    margin-bottom: 1rem;
    vertical-align: middle;
    p {
      display: block;
      font-size: 2.5rem;
      font-weight: 600;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      margin: 0;
    }
    p:nth-of-type(1) {
      left: 0;
    }
    p:nth-of-type(2) {
      left: 14rem;
    }
    input {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      margin-top: 0.2rem;
      left: 7.5rem;
      width: 5rem;
      height: 3rem;
      font-size: 3rem;
      font-weight: 600;
      outline: none;
      text-align: center;
      border: none;
      border-bottom: 0.1rem solid #000;
      background: none;
    }
    .buttonGroup {
      position: absolute;
      width: 10rem;
      height: auto;
      top: 50%;
      transform: translateY(-50%);
      right: 0rem;
      button {
        margin: 0.1rem;
        float: left;
        font-size: 1.6rem;
        width: 4rem;
        height: 2.5rem;
        cursor: pointer;
      }
    }
  }
}
