.select {
  width: 100%;
  height: 16rem;
  padding: 2rem 0;
  margin: 0 auto;
  display: flex;
  border-bottom: 1px solid #333;
  form {
    margin: 0 auto;
    vertical-align: middle;
    position: relative;
    input {
      left: 50%;
      top: 3rem;
      height: 3rem;
      transform: translate(-50%, -50%);
      position: absolute;
      text-align: center;
      font-size: 1.8rem;
      font-weight: 500;
      border: none;
      border-bottom: 0.1rem solid #000;
    }
    input:focus {
      outline: none;
    }
    datalist {
      width: 100%;
      option {
        width: 100%;
        cursor: pointer;
        text-align: center;
        left: 0;
      }
    }
  }
  .start {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 16rem;
    width: 8rem;
    height: 3rem;
    border: 0.1rem solid #333;
    background: none;
    word-break: keep-all;
    cursor: pointer;
  }
}

.try {
  padding-top: 10px;
  width: 32rem;
  margin: 0 auto;
  h2 {
    margin: 0;
    padding: 0;
  }
  set {
    display: flex;
    h3 {
      margin-top: 0;
      display: block;
      width: 100px;
    }
  }
}
